.inputBox {
  border-radius: 8px;
  border: 1px solid #404040;
  color: #cfcccc;
  font-family: Poppins;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-bottom: 10px;
}
::placeholder {
  color: #cfcccc;
  font-size: 16px;
}
.inputBox:focus {
  outline: none;
}
